import React from "react";
import "./loginForm.module.css"
import v from '../../../img/V.svg'
import { useDispatch, useSelector } from "react-redux";
import { RType } from "../../../store/reducers/reducers";
import { bindActionCreators } from "redux";
import * as LoginFuncs from '../../../store/functions/flogin'
import { ILogin } from "../../../models/login";



const LoginForm: React.FC = () => {
    let state: ILogin
    const dispatch = useDispatch()
    const { email, password, remember, error } = state = useSelector((state: RType) => state.login)
    const { LoginOnChangeEmail, LoginOnChangePass, LoginOnChangeRemember, Login } = bindActionCreators(LoginFuncs, dispatch)

    return (
        <div className="auth_form">
            <div className="auth_content">
                <div className="auth_form_title">
                    –†–µ–≥–∏—Å—Ç—Ä–∞—Ü–∏—è
                </div>
                <div className="auth_form_inputs">
                    <input
                        value={email}
                        onChange={(e) => { LoginOnChangeEmail(e.target.value) }}
                        placeholder="–í–≤–µ–¥–∏—Ç–µ –≤–∞—à e-mail" type="email" />
                    <input
                        value={password}
                        onChange={(e) => { LoginOnChangePass(e.target.value) }}
                        placeholder="–í–≤–µ–¥–∏—Ç–µ –≤–∞—à –ø–∞—Ä–æ–ª—å" type="password" />
                </div>
                <div className="auth_form_input">
                    {
                        error ?
                            <div className="form_input_error login_error">{error}</div>
                            :
                            <>
                            </>
                    }
                    <div
                        onClick={() => { LoginOnChangeRemember(!remember) }}
                        className="auth_form_agreement">
                        <div
                            style={remember ? { background: "#0056D3" } : { background: "#FFF" }}
                            className="agreement_checkbox">
                            <img src={v} alt="" />
                        </div>
                        <div className="agreement_text">–ó–∞–ø–æ–º–Ω–∏—Ç—å –º–µ–Ω—è</div>
                    </div>
                </div>

                <div
                    onClick={() => { Login(state) }}
                    className="auth_form_btn width_btn50 ">
                    –í–æ–π—Ç–∏
                </div>
            </div>
        </div>
    )
}

export default LoginForm