import React from "react";
import { useSelector } from "react-redux";
import { RType } from "../../../store/reducers/reducers";
import AuthPresent from "../../blocks/auth_present/auth_present";
import RegistrationForm from "../../blocks/registrationForm/registrationForm";
import "./registration.module.css"

const Registration: React.FC = () => {

    const { loading } = useSelector((state: RType) => state.registration)

    return (
        <div className="registration">
            <div className="container">
                <div className="auth_block">

                    <AuthPresent img={1} />


                    <RegistrationForm />


                </div>
            </div>
        </div>
    )
}

export default Registration