import { ILogin, LoginFormAction, LoginFormActionTypes } from "../../models/login";

const initialState: ILogin = {
    email: '',
    password: '',
    remember: false,
    error: '',
    login: false,
    loading: false
}

export const RLogin = (
    state = initialState,
    action: LoginFormAction): ILogin => {
    switch (action.type) {
        case LoginFormActionTypes.OnChange_Email:
            return {
                ...state,
                email: action.payload
            }
        case LoginFormActionTypes.OnChange_Pass:
            return {
                ...state,
                password: action.payload
            }
        case LoginFormActionTypes.OnClick_Remember:
            return {
                ...state,
                remember: action.payload
            }
        case LoginFormActionTypes.Login:
            return {
                ...state,
                email: '',
                password: '',
                remember: false,
                error: '',
                login: true,
            }
        case LoginFormActionTypes.LogOut:
            return {
                ...state,
                login: false
            }
        case LoginFormActionTypes.Login_Error:
            return {
                ...state,
                error: action.payload
            }
        case LoginFormActionTypes.LoginLoading:
            return {
                ...state,
                loading: action.payload
            }
        default:
            return state
    }
}