import { IOrders, IOrdersAction, OrdersActionTypes } from "../../models/orders";

const initialState: IOrders = {
    orders: [],
    orders_type: ''
}

export const ROrders = (
    state = initialState,
    action: IOrdersAction): IOrders => {
    switch (action.type) {
        case OrdersActionTypes.UserOrders:
            return {
                orders: action.payload,
                orders_type: 'user'
            }
        case OrdersActionTypes.SearchOrders:
            return {
                orders: action.payload,
                orders_type: 'search'
            }

        case OrdersActionTypes.ReloadOrders:
            return {
                orders: [],
                orders_type: ''
            }
        default:
            return state
    }
}