import React, { useState } from "react";
import "./registrationForm.module.css"

import v from '../../../img/V.svg'
import { useDispatch, useSelector } from "react-redux";
import { RType } from "../../../store/reducers/reducers";
import { bindActionCreators } from "redux";

import * as RegFuncs from '../../../store/functions/fregistration'
import { IRegistration } from "../../../models/registration";


const RegistrationForm: React.FC = () => {
    let state: IRegistration
    const dispatch = useDispatch()
    const { name, password, email, phone, rols, check, loading } = state = useSelector((state: RType) => state.registration)
    const { RegOnChangeName, RegOnChangePass, RegOnChangeEmail, RegOnChangePhone, RegOnClickRol, RegOnClickCheckbox, Registration } = bindActionCreators(RegFuncs, dispatch)


    return (
        <div className="auth_form">
            {
                loading ?
                    <div>
                        Loading...
                    </div>
                    :
                    <div className="auth_content">
                        <div className="auth_form_title">
                            –†–µ–≥–∏—Å—Ç—Ä–∞—Ü–∏—è
                        </div>
                        <div className="auth_form_inputs">
                            <div className="auth_form_input">
                                <input
                                    onChange={(e) => RegOnChangeName(e.target.value)}
                                    value={name.value}
                                    placeholder="–í–∞—à–µ –∏–º—è"
                                    type="name" />
                                {
                                    name.error ?
                                        <div className="form_input_error reg_error">{name.error}</div>
                                        :
                                        <>
                                        </>
                                }
                            </div>
                            <div className="auth_form_input">
                                <input
                                    onChange={(e) => RegOnChangePass(e.target.value)}
                                    value={password.value}
                                    placeholder="–ü—Ä–∏–¥—É–º–∞–π—Ç–µ –ø–∞—Ä–æ–ª—å"
                                    type="text" />
                                {
                                    password.error ?
                                        <div className="form_input_error reg_error">{password.error}</div>
                                        :
                                        <>
                                        </>
                                }
                            </div>
                            <div className="auth_form_input">
                                <input
                                    onChange={(e) => RegOnChangeEmail(e.target.value)}
                                    value={email.value}
                                    placeholder="–í–∞—à e-mail"
                                    type="email" />
                                {
                                    email.error ?
                                        <div className="form_input_error reg_error">{email.error}</div>
                                        :
                                        <>
                                        </>
                                }
                            </div>
                            <div className="auth_form_input">
                                <input
                                    onChange={(e) => RegOnChangePhone(e.target.value)}
                                    value={phone.value}
                                    placeholder="–í–∞—à –Ω–æ–º–µ—Ä —Ç–µ–ª–µ—Ñ–æ–Ω–∞"
                                    type="tel" />
                                {
                                    phone.error ?
                                        <div className="form_input_error reg_error">{phone.error}</div>
                                        :
                                        <>
                                        </>
                                }
                            </div>
                            <div className="auth_form_group">
                                <div className="form_one_line">
                                    {rols[0]}
                                </div>
                                <div className="form_group_list">
                                    {
                                        rols.map(rol =>
                                            <div onClick={(e) => { e.stopPropagation(); RegOnClickRol(rols, rol) }} key={rol} >{rol}</div>
                                        )
                                    }
                                </div>
                            </div>
                        </div>
                        <div className="auth_form_input">
                            <div
                                onClick={() => RegOnClickCheckbox(!check.value)}
                                className="auth_form_agreement">
                                <div
                                    style={check.value ? { background: "#0056D3" } : { background: "#FFF" }}
                                    className="agreement_checkbox">
                                    <img src={v} alt="" />
                                </div>
                                <div className="agreement_text">–Ø —Å–æ–≥–ª–∞—Å–µ–Ω –Ω–∞ –æ–±—Ä–∞–±–æ—Ç–∫—É –ø–µ—Ä—Å–æ–Ω–∞–ª—å–Ω—ã—Ö –¥–∞–Ω–Ω—ã—Ö</div>
                            </div>
                            {
                                check.error ?
                                    <div className="form_input_error reg_error">{check.error}</div>
                                    :
                                    <>
                                    </>
                            }
                        </div>
                        <div
                            onClick={() => { Registration(state) }}
                            className="auth_form_btn">
                            –ó–∞—Ä–µ–≥–µ—Å—Ç—Ä–∏—Ä–æ–≤–∞—Ç—å—Å—è
                        </div>
                    </div>
            }
        </div>
    )
}

export default RegistrationForm