import React, { useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { RType } from "../../../store/reducers/reducers";
import "./engineer_profile.module.css"
import user from "../../../img/user.png"
import { NavLink } from "react-router-dom";
import BlurMoney from "../../blocks/blur_money/blur_money";
import { bindActionCreators } from "redux";
import * as OrdersFuncs from '../../../store/functions/forders'


const EngineerProfile: React.FC = () => {
    const dispatch = useDispatch()
    const { UserOrders } = bindActionCreators(OrdersFuncs, dispatch)

    const [money_list, setMoney] = useState(false)
    const { name, money, education, experience } = useSelector((state: RType) => state.user)



    return (
        <div className="profile">
            <div className="container">
                <div className="profile_block">
                    <div className="profile_content">
                        <div className="profile_data">
                            <div className="profile_ava">
                                <img src={user} alt="" />
                            </div>
                            <div>
                                <div className="user_name">{name}</div>
                                <div>–ö–æ—à–µ–ª—ë–∫: {money} ‚ÇΩ</div>
                                <div>–û–±—Ä–∞–∑–æ–≤–∞–Ω–∏–µ: {education}</div>
                                <div className="user_experience">–û–ø—ã—Ç —Ä–∞–±–æ—Ç—ã {experience} –ª–µ—Ç</div>
                            </div>
                        </div>
                        <div className="profile_menu">


                            <div className="profile_menu_orders engineer_menu_orders">
                                <div className="list_menu_orders">
                                    –ó–∞–∫–∞–∑—ã –≤ —Ä–∞–±–æ—Ç–µ
                                </div>
                                <div className="order_list">
                                    <NavLink onClick={() => { UserOrders('agreed') }} to="/orders" >
                                        <div className="order_line">–°–æ–≥–ª–∞—Å–æ–≤–∞–Ω–Ω—ã–µ –∑–∞–∫–∞–∑—ã</div>
                                    </NavLink>
                                    <NavLink onClick={() => { UserOrders('inconsistent') }} to="/orders" >
                                        <div className="order_line">–ù–µ—Å–æ–≥–ª–∞—Å–æ–≤–∞–Ω–Ω—ã–µ –∑–∞–∫–∞–∑—ã</div>
                                    </NavLink>
                                    <NavLink onClick={() => { UserOrders('waiting') }} to="/orders" >
                                        <div className="order_line">–ó–∞–∫–∞–∑—ã –≤ –æ–∂–∏–¥–∞–Ω–∏–∏</div>
                                    </NavLink>
                                </div>
                            </div>
                            <div onClick={() => { setMoney(true) }} className="list_menu_money">–í—ã–≤–µ—Å—Ç–∏ —Å—Ä–µ–¥—Å—Ç–≤–∞</div>
                            <NavLink onClick={() => { UserOrders('completed') }} to="/orders" >
                                <div className="list_menu_over">–í—ã–ø–æ–ª–Ω–µ–Ω–Ω—ã–µ —Ä–∞–±–æ—Ç—ã</div>
                            </NavLink>
                            <div className="list_menu_edit no_release">–†–µ–¥–∞–∫—Ç–∏—Ä–æ–≤–∞—Ç—å –∏–Ω—Ñ–æ—Ä–º–∞—Ü–∏—é</div>
                        </div>
                    </div>
                </div>
            </div>
            {
                money_list ?
                    <BlurMoney fync={setMoney} />
                    :
                    <>
                    </>
            }

        </div>

    )
}

export default EngineerProfile