import { IRegistration, RegFormAction, RegFormActionTypes } from "../../models/registration"

const initialState: IRegistration = {
    name: {
        value: '',
        error: ''
    },
    password: {
        value: '',
        error: ''
    },
    email: {
        value: '',
        error: ''
    },
    phone: {
        value: '',
        error: ''
    },
    rols: ['–Ø –∏–Ω–∂–∏–Ω–µ—Ä', '–Ø –ø–æ–¥—Ä—è–¥—á–∏–∫', '–Ø –∑–∞–∫–∞–∑—á–∏–∫'],
    check: {
        value: false,
        error: ''
    },
    loading: false
}

export const RRegistration = (
    state = initialState,
    action: RegFormAction): IRegistration => {
    switch (action.type) {
        case RegFormActionTypes.OnChange_Name:
            return {
                ...state,
                name: {
                    error: '',
                    value: action.payload
                }
            }
        case RegFormActionTypes.OnChange_Pass:
            return {
                ...state,
                password: {
                    error: '',
                    value: action.payload
                }
            }
        case RegFormActionTypes.OnChange_Email:
            return {
                ...state,
                email: {
                    error: '',
                    value: action.payload
                }
            }
        case RegFormActionTypes.OnChange_Phone:
            return {
                ...state,
                phone: {
                    error: '',
                    value: action.payload
                }
            }
        case RegFormActionTypes.OnClick_Rol:
            return {
                ...state,
                rols: action.payload
            }
        case RegFormActionTypes.OnClick_Checkbox:
            return {
                ...state,
                check: {
                    error: '',
                    value: action.payload
                }
            }

        case RegFormActionTypes.Registration:
            return {
                ...state,
                name: {
                    value: '',
                    error: ''
                },
                password: {
                    value: '',
                    error: ''
                },
                email: {
                    value: '',
                    error: ''
                },
                phone: {
                    value: '',
                    error: ''
                },
                rols: ['–Ø –∏–Ω–∂–∏–Ω–µ—Ä', '–Ø –ø–æ–¥—Ä—è–¥—á–∏–∫', '–Ø –∑–∞–∫–∞–∑—á–∏–∫'],
                check: {
                    value: false,
                    error: ''
                }
            }


        case RegFormActionTypes.RegistrationError:
            return {
                ...state,
                name: {
                    ...state.name,
                    error: action.payload.nameErr
                },

                password: {
                    ...state.password,
                    error: action.payload.passErr
                },
                email: {
                    ...state.email,
                    error: action.payload.emailErr
                },
                phone: {
                    ...state.phone,
                    error: action.payload.phoneErr
                },
                check: {
                    ...state.check,
                    error: action.payload.checkboxErr
                }
            }
        case RegFormActionTypes.RegLoading:
            return {
                ...state,
                loading: action.payload
            }
        default:
            return state
    }

}