import { combineReducers } from "redux";
import { RUser } from "./ruser";
import { RLogin } from "./rlogin";
import { RRegistration } from "./rregistration";
import { ROrders } from "./rorders";
import { ROrder } from "./rorder";


export const Redusers = combineReducers({
    registration: RRegistration,
    login: RLogin,
    user: RUser,
    orders: ROrders,
    order: ROrder
})

export type RType = ReturnType<typeof Redusers>