import React from "react";
import "./engineer_orders.module.css"

import fire from '../../../img/fire.svg'
import green from '../../../img/green.svg'
import yellow from '../../../img/yellow.svg'
import red from '../../../img/red.svg'
import { useDispatch, useSelector } from "react-redux";
import { RType } from "../../../store/reducers/reducers";
import { NavLink } from "react-router-dom";
import { bindActionCreators } from "redux";
import * as OrderFuncs from '../../../store/functions/forder'

const color = new Map([
    ["green", green],
    ["yellow", yellow],
    ["red", red]
])

const EngineerOrders: React.FC = () => {

    let path: string
    const { orders_type, orders } = useSelector((state: RType) => state.orders)
    const dispatch = useDispatch()
    const { GetOrder } = bindActionCreators(OrderFuncs, dispatch)

    if (orders_type === "search") {
        path = '/details'
    }
    else {
        path = '/order'
    }

    return (
        <div className="orders">
            <div className="container">
                <div className="orders_blocks">
                    {
                        orders.map(order =>
                            <NavLink onClick={() => { GetOrder(order) }} to={path}>
                                <div key={order.id} className="search_order_block">
                                    <div>
                                        <div className="order_block_title">
                                            –®–∏—Ñ—Ä –ø—Ä–æ–µ–∫—Ç–∞:
                                        </div>
                                        <div>{order.id}</div>
                                    </div>
                                    <div>
                                        <div className="order_block_title">
                                            –†–∞–∑–¥–µ–ª:
                                        </div>
                                        <div>{order.—Åhapter}</div>
                                    </div>
                                    <div className="order_block_time">
                                        <div>
                                            <div className="order_block_title">
                                                –°—Ä–æ–∫:
                                            </div>
                                            <div>{order.days} –¥–Ω–µ–π</div>
                                        </div>
                                        <div className="block_time_hot">
                                            {
                                                order.hot ?
                                                    <>
                                                        <img src={fire} alt="" />
                                                        <div>
                                                            –°—Ä–æ—á–Ω—ã–π –ø—Ä–æ–µ–∫—Ç
                                                        </div>
                                                    </>
                                                    :
                                                    <>
                                                    </>
                                            }
                                        </div>
                                    </div>
                                    <div className="order_block_details">
                                        <div className="order_block_title">
                                            –û–ø–∏—Å–∞–Ω–∏–µ –ø—Ä–æ–µ–∫—Ç–∞:
                                        </div>
                                        <div>
                                            {order.text}
                                        </div>
                                    </div>
                                    <div className="order_block_more">–ü–æ–¥—Ä–æ–±–Ω–µ–µ</div>
                                    <div className="order_block_colour">
                                        <img src={color.get(order.color)} alt="" />
                                    </div>
                                </div>
                            </NavLink>
                        )
                    }
                </div>
            </div>
            <div className="orders_lists">
                <div className="active_list">1</div>
                <div>2</div>
                <div>3</div>
                <div>...</div>
                <div>10</div>
            </div>
        </div>

    )
}

export default EngineerOrders