import React, { useEffect, useState } from "react";
import "./contractor_order.module.css"
import slider_RIGHT from '../../../img/slider_RIGHT.svg'
import slider_LEFT from '../../../img/slider_LEFT.svg'
import blue_star from '../../../img/blue_star.svg'
import white_star from '../../../img/white_star.svg'
import V from '../../../img/V.svg'
import green from '../../../img/green.svg'
import yellow from '../../../img/yellow.svg'
import red from '../../../img/red.svg'
import fire from '../../../img/fire.svg'
import BlurEngineerDocuments from "../../blocks/blur_Engineer_documents/blur_engineer_documents";

const ContractorOrder: React.FC = () => {

    const [docEng, setAddEng] = useState(false)
    let eng = [1, 2, 3, 4, 5, 6, 7]


    useEffect(() => {
        let slider_left: HTMLDivElement = document.querySelector('.slider_left')!
        let slider_right: HTMLDivElement = document.querySelector('.slider_right')!
        let slider_window: HTMLDivElement = document.querySelector('.sub_order')!



        let users_blocks = document.querySelectorAll('.sub_order_block')

        let slider_list: HTMLDivElement = document.querySelector('.slider_users_list')!

        let slider_click = 0


        for (let index = 0; index < users_blocks.length; index++) {
            if (index < slider_click || index > slider_click + 3) {
                users_blocks[index].classList.add('grey_block')
                users_blocks[index].querySelector('.sub_block_active')!.classList.add('bg_grey')

            }
        }

        if (users_blocks.length <= 4) {
            slider_left.querySelector('img')!.style.display = 'none'
            slider_left.style.cursor = 'default'
            slider_right.querySelector('img')!.style.display = 'none'
            slider_right.style.cursor = 'default'
        }


        slider_window.style.height = users_blocks[slider_click] ? `${users_blocks[slider_click].scrollHeight + 20}px` : '0px'

        slider_right.addEventListener('click', () => {
            if (slider_click < users_blocks.length - 4) {
                slider_click += 1
                slider_list.style.left = `calc((((70vw - 6vw) / -4) - 2vw) * ${slider_click})`

                for (let index = 0; index < users_blocks.length; index++) {
                    if (index < slider_click || index > slider_click + 3) {
                        users_blocks[index].classList.add('grey_block')
                        users_blocks[index].querySelector('.sub_block_active')!.classList.add('bg_grey')

                    } else {
                        users_blocks[index].classList.remove('grey_block')
                        users_blocks[index].querySelector('.sub_block_active')!.classList.remove('bg_grey')
                    }
                }
            }
        })



        slider_left.addEventListener('click', () => {
            if (slider_click > 0) {
                slider_click -= 1
                slider_list.style.left = `calc((((70vw - 6vw) / -4) - 2vw) * ${slider_click})`


                for (let index = 0; index < users_blocks.length; index++) {
                    if (index < slider_click || index > slider_click + 3) {
                        users_blocks[index].classList.add('grey_block')
                        users_blocks[index].querySelector('.sub_block_active')!.classList.add('bg_grey')

                    } else {
                        users_blocks[index].classList.remove('grey_block')
                        users_blocks[index].querySelector('.sub_block_active')!.classList.remove('bg_grey')
                    }
                }
            }

        })


        window.addEventListener('resize', () => {
            slider_window.style.height = users_blocks[slider_click] ? `${users_blocks[slider_click].scrollHeight + 20}px` : '0px'
        });
    }, [])

    return (
        <>
            <div className="open_order">
                <div className="container">
                    <div className="open_order_block">

                        <div className="order_block_header">
                            <div>–ó–∞–∫–∞–∑ #1</div>
                            <div>–û–û–û ‚Äú–ê–°–ò‚Äù</div>
                        </div>
                        <div className="order_block_content">
                            <div className="left_block_content">
                                <div>–û–ø–∏—Å–∞–Ω–∏–µ –ø—Ä–æ–µ–∫—Ç–∞, –æ–ø–∏—Å–∞–Ω–∏–µ –ø—Ä–æ–µ–∫—Ç–∞, –æ–ø–∏—Å–∞–Ω–∏–µ –ø—Ä–æ–µ–∫—Ç–∞. –û–ø–∏—Å–∞–Ω–∏–µ –ø—Ä–æ–µ–∫—Ç–∞, –æ–ø–∏—Å–∞–Ω–∏–µ –ø—Ä–æ–µ–∫—Ç–∞</div>
                                <div className="left_block_engineers">
                                    <div className="left_engineers_list">–ò—Å–ø–æ–ª–Ω–∏—Ç–µ–ª–∏, —Ä–∞–±–æ—Ç–∞—é—â–∏–µ –Ω–∞–¥ –ø—Ä–æ–µ–∫—Ç–æ–º:</div>
                                    <ol>
                                        <li>
                                            <div>
                                                –ê–Ω–¥—Ä–µ–π
                                                <div className="user_stars">
                                                    <img src={blue_star} alt="" />
                                                    <img src={blue_star} alt="" />
                                                    <img src={blue_star} alt="" />
                                                    <img src={blue_star} alt="" />
                                                    <img src={white_star} alt="" />
                                                </div>
                                            </div>
                                        </li>
                                        <li>
                                            <div>
                                                –ê–Ω–¥—Ä–µ–π
                                                <div className="user_stars">
                                                    <img src={blue_star} alt="" />
                                                    <img src={blue_star} alt="" />
                                                    <img src={blue_star} alt="" />
                                                    <img src={blue_star} alt="" />
                                                    <img src={white_star} alt="" />
                                                </div>
                                            </div>
                                        </li>
                                        <li>
                                            <div>
                                                –ê–Ω–¥—Ä–µ–π
                                                <div className="user_stars">
                                                    <img src={blue_star} alt="" />
                                                    <img src={blue_star} alt="" />
                                                    <img src={blue_star} alt="" />
                                                    <img src={blue_star} alt="" />
                                                    <img src={white_star} alt="" />
                                                </div>
                                            </div>
                                        </li>
                                        <li>
                                            <div>
                                                –ê–Ω–¥—Ä–µ–π
                                                <div className="user_stars">
                                                    <img src={blue_star} alt="" />
                                                    <img src={blue_star} alt="" />
                                                    <img src={blue_star} alt="" />
                                                    <img src={blue_star} alt="" />
                                                    <img src={white_star} alt="" />
                                                </div>
                                            </div>
                                        </li>
                                    </ol>
                                </div>
                            </div>
                            <div className="block_color_meny">
                                <div className="color_meny_fire"><img src={fire} alt="" />–°—Ä–æ—á–Ω—ã–π –∑–∞–∫–∞–∑</div>
                                <div className="color_meny_red"><img src={red} alt="" />–í—ã–ø–æ–ª–Ω–µ–Ω–æ –Ω–∞ 100%, —É—Å—Ç—Ä–∞–Ω–µ–Ω–∏–µ –∑–∞–º–µ—á–∞–Ω–∏–π</div>
                                <div className="color_meny_yellow"><img src={yellow} alt="" />–í—ã–ø–æ–ª–Ω–µ–Ω–æ –Ω–∞ 50%</div>
                                <div className="color_meny_green"><img src={green} alt="" />–ù–æ–≤—ã–π –∑–∞–∫–∞–∑</div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>


            <div className="sub_order">
                <div className="sub_order_slider">

                    <div className="slider_left sub_slider">
                        <img src={slider_LEFT} alt="" />
                    </div>

                    <div className="slider_users_window">

                        <div className="slider_users_list">


                            {
                                eng.map(e =>
                                    <div key={e} className="sub_order_block">
                                        <div className="sub_block">
                                            <div className="sub_block_title ">–ê–ª–µ–∫—Å–∞–Ω–¥—Ä –õ–µ–æ–Ω–∏–¥–æ–≤–∏—á –°–∏–≤—è–∫–æ–≤</div>
                                            <div className="sub_block_stars">
                                                <img src={blue_star} alt="" />
                                                <img src={blue_star} alt="" />
                                                <img src={blue_star} alt="" />
                                                <img src={blue_star} alt="" />
                                                <img src={white_star} alt="" />
                                            </div>
                                            <div className="sub_block_text">450 —Ä—É–±/—á</div>
                                            <div onClick={() => { setAddEng(true) }} className="sub_blue_button">–í—ã–±—Ä–∞—Ç—å</div>
                                        </div>
                                        <div className="sub_block_active"></div>
                                    </div>
                                )
                            }


                        </div>
                    </div>
                    <div className="slider_right sub_slider">
                        <img src={slider_RIGHT} alt="" />
                    </div>

                </div>
            </div>

            {
                docEng ?
                    <BlurEngineerDocuments func={setAddEng} />
                    :
                    <>
                    </>
            }

        </>
    )
}

export default ContractorOrder
