import { strict } from "assert";
import axios from "axios";
import { Dispatch } from "react"
import { IRegistration, RegFormAction, RegFormActionTypes } from "../../models/registration";


export const RegOnChangeName = (value: string): RegFormAction => {
    return {
        type: RegFormActionTypes.OnChange_Name,
        payload: value
    }
}


export const RegOnChangePass = (value: string): RegFormAction => {
    return {
        type: RegFormActionTypes.OnChange_Pass,
        payload: value
    }
}

export const RegOnChangeEmail = (value: string): RegFormAction => {
    return {
        type: RegFormActionTypes.OnChange_Email,
        payload: value
    }
}

export const RegOnChangePhone = (value: string): RegFormAction | void => {
    let tel

    if (value.length >= 12) {
        tel = value.slice(0, 12)
    }
    else if (value[1] === '7' && Number(value)) {
        tel = value
    }
    else if (!Number(value)) {
        tel = '+7'
    }
    else if (value[0] !== '+') {
        tel = `+7${value}`
    }
    else {
        tel = '+7'
    }


    return {
        type: RegFormActionTypes.OnChange_Phone,
        payload: tel
    }
}

export const RegOnClickRol = (value: string[], choice: string): RegFormAction => {

    return {
        type: RegFormActionTypes.OnClick_Rol,
        payload: [choice, ...value.filter(v => v !== choice)]
    }
}

export const RegOnClickCheckbox = (value: boolean): RegFormAction => {
    return {
        type: RegFormActionTypes.OnClick_Checkbox,
        payload: value
    }
}



const RegValidationName = (name: string): string => {
    if (!name) {
        return '–ü–æ–ª–µ –æ–±—è–∑–∞—Ç–µ–ª—å–Ω–æ –¥–ª—è –≤–≤–æ–¥–∞'
    }
    else {
        return ''
    }
}

const RegValidationPass = (pass: string): string => {
    let regex = /^(?=.*[a-z])(?=.*[A-Z])(?=.*\d)[a-zA-Z\d]{8,14}$/

    if (!pass) {
        return '–ü–æ–ª–µ –æ–±—è–∑–∞—Ç–µ–ª—å–Ω–æ –¥–ª—è –≤–≤–æ–¥–∞'
    }
    else if (!regex.test(pass)) {
        return '–ü–∞—Ä–æ–ª—è –¥–æ–ª–∂–µ–Ω –±—ã—Ç—å –±—É–∫–≤–µ–Ω–Ω–æ-—Ü–∏—Ñ—Ä–æ–≤—ã–º —Å –º–∏–Ω–∏–º—É–º –æ–¥–Ω–æ–π –∑–∞–≥–ª–∞–≤–Ω–æ–π –±—É–∫–≤–æ–π –∏ —Å–æ–¥–µ—Ä–∂–∞—Ç—å –æ—Ç 8 –¥–æ 14 —Å–∏–º–≤–æ–ª–æ–≤'
    }
    else {
        return ''
    }
}

const RegValidationEmail = (email: string): string => {
    let regex = /^([a-z0-9_-]+\.)*[a-z0-9_-]+@[a-z0-9_-]+(\.[a-z0-9_-]+)*\.[a-z]{2,6}$/
    if (!email) {
        return '–ü–æ–ª–µ –æ–±—è–∑–∞—Ç–µ–ª—å–Ω–æ –¥–ª—è –≤–≤–æ–¥–∞'
    }
    else if (!regex.test(email)) {
        return '–í–≤–µ–¥–µ–Ω—ã –Ω–µ–∫–æ—Ä—Ä–µ–∫—Ç–Ω—ã–µ –¥–∞–Ω–Ω—ã–µ'
    }
    else {
        return ''
    }
}

const RegValidationPhone = (phone: string): string => {
    let regex = /^(\s*)?(\+)?([- _():=+]?\d[- _():=+]?){10,14}(\s*)?$/
    if (!phone) {
        return '–ü–æ–ª–µ –æ–±—è–∑–∞—Ç–µ–ª—å–Ω–æ –¥–ª—è –≤–≤–æ–¥–∞'
    }
    else if (!regex.test(phone)) {
        return '–í–≤–µ–¥–µ–Ω—ã –Ω–µ–∫–æ—Ä—Ä–µ–∫—Ç–Ω—ã–µ –¥–∞–Ω–Ω—ã–µ'
    }
    else {
        return ''
    }
}

const RegValidationCheckbox = (agreement: boolean): string => {
    if (!agreement) {
        return '–î–ª—è —Ä–µ–≥–∏—Å—Ç—Ä–∞—Ü–∏–∏ –Ω–µ–æ–±—Ö–æ–¥–∏–º–æ –≤–∞—à–µ —Å–æ–≥–ª–∞—Å–∏–µ'
    }
    else {
        return ''
    }
}


export const Registration = (state: IRegistration) => {
    let name_error = RegValidationName(state.name.value)
    let password_error = RegValidationPass(state.password.value)
    let email_error = RegValidationEmail(state.email.value)
    let phone_error = RegValidationPhone(state.phone.value)
    let check_error = RegValidationCheckbox(state.check.value)



    let error = name_error
        || password_error
        || email_error
        || phone_error
        || check_error


    if (error) {
        return (dispatch: Dispatch<RegFormAction>) => {
            dispatch({
                type: RegFormActionTypes.RegistrationError,
                payload: {
                    nameErr: name_error,
                    passErr: password_error,
                    emailErr: email_error,
                    phoneErr: phone_error,
                    checkboxErr: check_error
                }
            })
        }
    }


    else {
        return async (dispatch: Dispatch<RegFormAction>) => {
            dispatch({
                type: RegFormActionTypes.RegLoading,
                payload: true
            })
            await axios.post('http://193.200.74.86:5000/api/registration',
                {
                    "name": state.name.value,
                    "password": state.password.value,
                    "email": state.email.value,
                    "phone": state.phone.value,
                    "rols": state.rols[0]
                }
            ).then(res => {
                localStorage.setItem('accessToken', res.data.tokens.accessToken)
                dispatch({
                    type: RegFormActionTypes.RegistrationError,
                    payload: {
                        nameErr: name_error,
                        passErr: password_error,
                        emailErr: email_error,
                        phoneErr: phone_error,
                        checkboxErr: '–ù–∞ —ç–ª–µ–∫—Ç—Ä–æ–Ω–Ω—É—é –ø–æ—á—Ç—É –≤–∞–º –Ω–∞–ø—Ä–∞–≤–ª–µ–Ω–æ –ø–∏—Å—å–º–æ –¥–ª—è –ø–æ–¥—Ç–≤–µ—Ä–∂–¥–µ–Ω–∏—è –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª—è'
                    }
                })

            }).catch(err => {
                if (err.response) {
                    dispatch({
                        type: RegFormActionTypes.RegistrationError,
                        payload: {
                            nameErr: name_error,
                            passErr: password_error,
                            emailErr: err.response.data.content.message,
                            phoneErr: phone_error,
                            checkboxErr: check_error
                        }
                    })
                }
                else {
                    dispatch({
                        type: RegFormActionTypes.RegistrationError,
                        payload: {
                            nameErr: name_error,
                            passErr: password_error,
                            emailErr: email_error,
                            phoneErr: phone_error,
                            checkboxErr: '–ü–æ–≤—Ç–æ—Ä–∏—Ç–µ –ø–æ–ø—ã—Ç–∫—É –ø–æ–∑–∂–µ'
                        }
                    })
                }
            }).finally(() => {
                dispatch({
                    type: RegFormActionTypes.RegLoading,
                    payload: false
                })
            })
        }
    }

}
