import { IOrder } from "./order";

export interface IOrders {
    orders: IOrder[],
    orders_type: string
}

export enum OrdersActionTypes {
    UserOrders = "UserOrders",
    SearchOrders = "SearchOrders",
    ReloadOrders = 'ReloadOrders'
}

interface I_UserOrders_Action {
    type: OrdersActionTypes.UserOrders,
    payload: IOrder[]
}

interface I_SearchOrders_Action {
    type: OrdersActionTypes.SearchOrders,
    payload: IOrder[]
}

interface I_ReloadOrders_Action {
    type: OrdersActionTypes.ReloadOrders,
}


export type IOrdersAction =
    I_UserOrders_Action |
    I_SearchOrders_Action |
    I_ReloadOrders_Action