export interface IUser {
    id: number
    name: string
    email: string
    phone: string
    money: number
    education: string
    experience: number
    reputation: number
    rol: string
}

export enum UserActionTypes {
    AcceptUser = 'AcceptUser',
    CleaningEngineer = 'CleaningUser'
}

interface I_AcceptUser_Action {
    type: UserActionTypes.AcceptUser
    payload: IUser
}


export type UserAction =
    I_AcceptUser_Action