import { IOrder, OrderAction, OrderActionTypes } from "../../models/order"

const initialState: IOrder = {
    id: 0,
    —Åhapter: '',
    days: 0,
    hot: false,
    color: '',
    text: '',
    contractor: '',
    users: [],
    documents: [],
    signature_engineer: false,
    signature_customer: false,
    price: 0
}


export const ROrder = (
    state = initialState,
    action: OrderAction): IOrder => {
    switch (action.type) {
        case OrderActionTypes.ShowOrder:
            return {
                ...state, ...action.payload
            }
        default:
            return state
    }

}