import React, { useEffect } from "react";
import "./blur_documents.module.css"

import exit_white from '../../../img/exit_white.svg'
import slider_LEFT from '../../../img/slider_LEFT.svg'
import slider_RIGHT from '../../../img/slider_RIGHT.svg'
import message from '../../../img/message.svg'
import zoom from '../../../img/zoom.svg'
import DOC2 from '../../../img/DOC2.png'

interface IBlurDocuments {
    func: (documents: boolean) => void
}

const BlurDocuments: React.FC<IBlurDocuments> = (props) => {

    useEffect(() => {
        let slider_left = document.querySelector('.document_slider_left')!
        let slider_right = document.querySelector('.document_slider_right')!
        let document_list: HTMLDivElement = document.querySelector('.slider_document_list')!
        let documents = document.querySelectorAll('.document_block')!
        let slider_clicks = 0

        for (let index = 0; index < documents.length; index++) {
            if (index < slider_clicks || index > slider_clicks) {
                documents[index].querySelector('.document_block_img')!.classList.add('document_img_active')
                documents[index].querySelector('.slider_block_grey')!.classList.add('bg_grey')
            }
        }

        slider_right.addEventListener('click', () => {
            if (slider_clicks != documents.length - 1) {
                slider_clicks += 1
                document_list.style.left = `calc(60vw * -${slider_clicks})`

                for (let index = 0; index < documents.length; index++) {
                    if (index < slider_clicks || index > slider_clicks) {
                        documents[index].querySelector('.document_block_img')!.classList.add('document_img_active')
                        documents[index].querySelector('.slider_block_grey')!.classList.add('bg_grey')
                    }
                    if (index === slider_clicks) {
                        documents[index].querySelector('.document_block_img')!.classList.remove('document_img_active')
                        documents[index].querySelector('.slider_block_grey')!.classList.remove('bg_grey')
                    }
                }
            }
        })


        slider_left.addEventListener('click', () => {
            if (slider_clicks != 0) {
                slider_clicks -= 1
                document_list.style.left = `calc(60vw * -${slider_clicks})`

                for (let index = 0; index < documents.length; index++) {
                    if (index < slider_clicks || index > slider_clicks) {
                        documents[index].querySelector('.document_block_img')!.classList.add('document_img_active')
                        documents[index].querySelector('.slider_block_grey')!.classList.add('bg_grey')
                    }
                    if (index === slider_clicks) {
                        documents[index].querySelector('.document_block_img')!.classList.remove('document_img_active')
                        documents[index].querySelector('.slider_block_grey')!.classList.remove('bg_grey')
                    }
                }
            }
        })
    }, [])

    return (
        <div className="blur">
            <div className="documents_slider_blosk">
                <div className="documents_slider_exit">
                    <div onClick={() => { props.func(false) }} > <img src={exit_white} alt="" /> </div>
                </div>
                <div className="documents_slider">
                    <div className="slider_left document_slider_left">
                        <img src={slider_LEFT} alt="" />
                    </div>

                    <div className="slider_documents_window">

                        <div className="slider_document_list">

                            <div className="document_block">
                                <div className="document_block_img">
                                    <img src={DOC2} alt="" />
                                    <div className="slider_block_grey"></div>
                                </div>
                            </div>

                            <div className="document_block">
                                <div className="document_block_img">
                                    <img src={DOC2} alt="" />
                                    <div className="slider_block_grey"></div>
                                </div>
                            </div>


                            <div className="document_block">
                                <div className="document_block_img">
                                    <img src={DOC2} alt="" />
                                    <div className="slider_block_grey"></div>
                                </div>
                            </div>


                            <div className="document_block">
                                <div className="document_block_img">
                                    <img src={DOC2} alt="" />
                                    <div className="slider_block_grey"></div>
                                </div>
                            </div>

                        </div>

                    </div>

                    <div className="slider_right document_slider_right">
                        <img src={slider_RIGHT} alt="" />
                    </div>


                </div>
                <div className="documents_slider_footer">
                    <div>
                        <div> <img src={message} alt="" /> </div>
                        <div> <img src={zoom} alt="" /> </div>
                    </div>
                </div>
            </div>

        </div>
    )
}

export default BlurDocuments