import React, { useState } from "react";
import "./engineer_order.module.css"

import blue_star from "../../../img/blue_star.svg"
import white_star from "../../../img/white_star.svg"
import fire from "../../../img/fire.svg"
import green from "../../../img/green.svg"
import yellow from "../../../img/yellow.svg"
import red from "../../../img/red.svg"
import { useSelector } from "react-redux";
import { RType } from "../../../store/reducers/reducers";
import BlurRecommend from "../../blocks/blur_recommend/blur_recommend";

const color = new Map([
    ["green", { color: green, content: '–ù–æ–≤—ã–π –∑–∞–∫–∞–∑' }],
    ["yellow", { color: yellow, content: '–í—ã–ø–æ–ª–Ω–µ–Ω–æ –Ω–∞ 50%' }],
    ["red", { color: red, content: '–í—ã–ø–æ–ª–Ω–µ–Ω–æ –Ω–∞ 100%, —É—Å—Ç—Ä–∞–Ω–µ–Ω–∏–µ –∑–∞–º–µ—á–∞–Ω–∏–π' }]
])

const EngineerOrder: React.FC = () => {
    const [recommend, setRecommend] = useState(false)

    const state = useSelector((state: RType) => state.order)
    let Stars = (r: number) => {
        let arr = []
        for (let index = 0; index < 5; index++) {
            if (index < r) {
                arr.push(blue_star)
            }
            else {
                arr.push(white_star)
            }
        }
        return (
            <>
                {
                    arr.map(star => <img src={star} alt="" />)
                }
            </>
        )
    }


    return (
        <div className="open_order">
            <div className="container">
                <div className="open_order_block">

                    <div className="order_block_header">
                        <div>–ó–∞–∫–∞–∑ #{state.id}</div>
                        <div>{state.contractor}</div>
                    </div>
                    <div className="order_block_content">
                        <div className="left_block_content">
                            <div>{state.text}</div>
                            <div className="left_block_engineers">
                                <div className="left_engineers_list">–ò—Å–ø–æ–ª–Ω–∏—Ç–µ–ª–∏, —Ä–∞–±–æ—Ç–∞—é—â–∏–µ –Ω–∞–¥ –ø—Ä–æ–µ–∫—Ç–æ–º:</div>
                                <ol>
                                    {
                                        state.users.map(user =>
                                            <li>
                                                <div>
                                                    {user.name}
                                                    <div className="user_stars">
                                                        {
                                                            Stars(user.reputation)
                                                        }
                                                    </div>
                                                </div>
                                            </li>
                                        )
                                    }
                                </ol>
                                <div onClick={() => { setRecommend(true) }} className="left_engineers_add">–ü–æ—Ä–µ–∫–æ–º–µ–Ω–¥–æ–≤–∞—Ç—å —Å–æ—Ç—Ä—É–¥–Ω–∏–∫–∞</div>
                            </div>
                            <div>
                                <div className="left_document_add">–ü—Ä–∏–∫—Ä–µ–ø–∏—Ç—å —Ñ–∞–π–ª—ã (3)</div>
                            </div>
                        </div>
                        <div className="block_color_meny">
                            {
                                state.hot ?
                                    <div className="color_meny_fire"><img src={fire} alt="" />–°—Ä–æ—á–Ω—ã–π –∑–∞–∫–∞–∑</div>
                                    :
                                    <>
                                    </>
                            }
                            <div className="color_meny_red"><img src={color.get(state.color)!.color} alt="" />{color.get(state.color)!.content}</div>

                        </div>
                    </div>
                    <div className="order_block_buttons">
                        <div className="block_button_blue">–û–ø—É–±–ª–∏–∫–æ–≤–∞—Ç—å</div>
                    </div>
                </div>
            </div>
            {
                recommend ?
                    <BlurRecommend func={setRecommend} />
                    :
                    <>
                    </>
            }

        </div>
    )
}

export default EngineerOrder