import React, { useEffect } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { BrowserRouter, Redirect, Route, Switch } from 'react-router-dom';
import { bindActionCreators } from 'redux';
import './App.css';
import Header from './components/blocks/header/header';
import Login from './components/lists/login/login';
import Registration from './components/lists/registration/registration';
import { routes } from './router/router';
import { RType } from './store/reducers/reducers';
import * as LoginFuncs from './store/functions/flogin'

function App() {

  const dispatch = useDispatch()
  const { CheckAuth } = bindActionCreators(LoginFuncs, dispatch)

  const { login, loading } = useSelector((state: RType) => state.login)
  const { rol } = useSelector((state: RType) => state.user)

  useEffect(() => {
    CheckAuth()
  }, [])

  if (loading) {
    return (
      <div style={{ background: "#fff" }} >
        –ó–∞–≥—Ä—É–∑–∫–∞...
      </div>
    )
  }

  // –ª–æ–≥–∏–∫–∞ –ø–æ –∑–∞–∫–∞–∑–∞–º —Å–¥–µ–ª–∞–Ω–∞ —Ç–æ–ª—å–∫–æ –¥–ª—è –∏–Ω–∂–∏–Ω–µ—Ä–æ–≤ (–ø–µ—Ä–µ–ª–∞–ø–∞—Ç–∏—Ç—å –¥–ª—è –≤—Å–µ—Ö —É—á–∞—Å—Ç–Ω–∏–∫–æ–≤ –ø–∞—Ä–∞–¥–∞)
  // –¥–ª—è –ø–æ–∏—Å–∫–∞ –∑–∞–∫–∞–∑–æ–≤ —Å–¥–µ–ª–∞—Ç—å –¥—Ä—É–≥–æ–π Store

  return (
    <BrowserRouter>
      <div className="App">
        <Header reg={login} />
        {
          login ?
            <Switch>
              {
                routes.get(rol)!.map(route =>
                  <Route key={route.path} component={route.componet} path={route.path} />)
              }
              <Redirect to='/profile' />
            </Switch>
            :
            <Switch>
              <Route component={Registration} path='/' exact />
              <Route component={Login} path='/login' exact />
              <Redirect to='/' />
            </Switch>
        }
      </div>
    </BrowserRouter >
  );
}

export default App;
