import axios from "axios"
import { Dispatch } from "redux"
import { UserAction, UserActionTypes } from "../../models/user"
import { ILogin, LoginFormAction, LoginFormActionTypes } from "../../models/login"


const $api = axios.create({
    withCredentials: true,
    headers: {
        'Access-Control-Allow-Origin': '*',
        'Content-Type': 'application/json',
        'Accept': 'application/json'
    }
})






export const LoginOnChangeEmail = (value: string): LoginFormAction => {
    return {
        type: LoginFormActionTypes.OnChange_Email,
        payload: value
    }
}

export const LoginOnChangePass = (value: string): LoginFormAction => {
    return {
        type: LoginFormActionTypes.OnChange_Pass,
        payload: value
    }
}

export const LoginOnChangeRemember = (value: boolean): LoginFormAction => {
    return {
        type: LoginFormActionTypes.OnClick_Remember,
        payload: value
    }
}




export const Login = (state: ILogin) => {
    return async (dispatch: Dispatch<LoginFormAction | UserAction>) => {
        dispatch({
            type: LoginFormActionTypes.LoginLoading,
            payload: true
        })
        await $api.post('http://193.200.74.86:5000/api/login',
            {
                "email": state.email,
                "password": state.password
            }
        ).then(res => {
            if (state.remember) {
                localStorage.setItem('accessToken', `Bearer ${res.data.tokens.accessToken}`)
            }
            dispatch({
                type: UserActionTypes.AcceptUser,
                payload: {
                    id: res.data.content.user.id,
                    name: res.data.content.user.name,
                    email: res.data.content.user.email,
                    phone: res.data.content.user.phone,
                    money: res.data.content.user.money || 0,
                    education: res.data.content.user.education || '',
                    experience: res.data.content.user.experience || 0,
                    reputation: res.data.content.user.reputation || 0,
                    rol: res.data.content.user.rols
                }
            })
            dispatch({
                type: LoginFormActionTypes.Login
            })
        }).catch(err => {
            if (err.response) {
                dispatch({
                    type: LoginFormActionTypes.Login_Error,
                    payload: err.response.data.content.message
                })
            }
            else {
                dispatch({
                    type: LoginFormActionTypes.Login_Error,
                    payload: '–ü–æ–≤—Ç–æ—Ä–∏—Ç–µ –ø–æ–ø—ã—Ç–∫—É –ø–æ–∑–∂–µ'
                })
            }
        }).finally(() => {
            dispatch({
                type: LoginFormActionTypes.LoginLoading,
                payload: false
            })
        })
    }
}

///
export const LogOut = () => {
    return async (dispatch: Dispatch<LoginFormAction>) => {
        localStorage.removeItem('accessToken')
        $api.post('http://193.200.74.86:5000/api/logout', {
            headers: {
                'Authorization': localStorage.getItem('accessToken')
            }
        })
        dispatch({
            type: LoginFormActionTypes.LogOut
        })
    }
}


export const CheckAuth = () => {
    return async (dispatch: Dispatch<LoginFormAction | UserAction>) => {
        if (localStorage.getItem('accessToken')) {
            dispatch({
                type: LoginFormActionTypes.LoginLoading,
                payload: true
            })
            await $api.get('http://193.200.74.86:5000/api/login',
                { headers: { 'authorization': localStorage.getItem('accessToken') } },
            ).then(res => {
                localStorage.setItem('accessToken', `Bearer ${res.data.tokens.accessToken}`)
                dispatch({
                    type: UserActionTypes.AcceptUser,
                    payload: {
                        id: res.data.content.user[0].id,
                        name: res.data.content.user[0].name,
                        email: res.data.content.user[0].email,
                        phone: res.data.content.user[0].phone,
                        money: res.data.content.user[0].money || 0,
                        education: res.data.content.user[0].education || '',
                        experience: res.data.content.user[0].experience || 0,
                        reputation: res.data.content.user[0].reputation || 0,
                        rol: res.data.content.user[0].rols
                    }
                })
                dispatch({
                    type: LoginFormActionTypes.Login
                })
            }).catch(async err => {
                await $api.post('http://193.200.74.86:5000/api/logout')
                localStorage.removeItem('accessToken')
                dispatch({
                    type: LoginFormActionTypes.LogOut
                })
            }).finally(() => {
                dispatch({
                    type: LoginFormActionTypes.LoginLoading,
                    payload: false
                })
            })
        }
    }
}