export interface ILogin {
    email: string
    password: string
    remember: boolean
    error: string
    login: boolean
    loading: boolean
}

export enum LoginFormActionTypes {
    OnChange_Email = 'OnChange_Email',
    OnChange_Pass = 'OnChenge_Pass',
    OnClick_Remember = 'OnClick_Remember',
    Login = 'Login',
    LogOut = 'LogOut',
    Login_Error = 'Login_Error',
    LoginLoading = 'LoginLoading'
}

interface I_OnChange_Email_Action {
    type: LoginFormActionTypes.OnChange_Email,
    payload: string
}

interface I_OnChange_Pass_Action {
    type: LoginFormActionTypes.OnChange_Pass
    payload: string
}

interface I_OnClick_Remember_Action {
    type: LoginFormActionTypes.OnClick_Remember
    payload: boolean
}

interface I_Login_Action {
    type: LoginFormActionTypes.Login
}
interface I_LogOut_Action {
    type: LoginFormActionTypes.LogOut
}

interface I_Login_Error_Action {
    type: LoginFormActionTypes.Login_Error
    payload: string
}
interface I_LoginLoading_Action {
    type: LoginFormActionTypes.LoginLoading
    payload: boolean
}

export type LoginFormAction =
    I_OnChange_Email_Action |
    I_OnChange_Pass_Action |
    I_OnClick_Remember_Action |
    I_Login_Action |
    I_Login_Error_Action |
    I_LogOut_Action |
    I_LoginLoading_Action