import React, { useEffect, useState } from "react";
import "./auth_present.module.css"
import log from '../../../img/log_present.png'
import reg from '../../../img/reg_present.png'

interface IAuthPresent {
    img: number
}

const AuthPresent: React.FC<IAuthPresent> = (props) => {

    let present
    if (props.img === 1) {
        present = reg
    }

    else {
        present = log
    }


    return (
        <div className="auth_present">
            <div className="auth_present_title">
                –ú–∞—Ä–∫–µ—Ç–ø–ª–µ–π—Å —Å—Ç—Ä–æ–∏—Ç–µ–ª—å–Ω–æ–π –¥–æ–∫—É–º–µ–Ω—Ç–∞—Ü–∏–∏
            </div>
            <div>
                <img src={present} alt="" />
            </div>
        </div>
    )
}

export default AuthPresent