import React, { useState } from "react";
import "./blur_money.module.css"
import exit from "../../../img/exit.svg"
import { useSelector } from "react-redux";
import { RType } from "../../../store/reducers/reducers";

interface IBlurMoney {
    fync: (money_list: boolean) => void
}

const BlurMoney: React.FC<IBlurMoney> = (props) => {
    const { money } = useSelector((state: RType) => state.user)
    const [getMoney, setGetMoney] = useState('')
    const [card, setCard] = useState('')
    const isNumeric = (value: string) => {
        return /^\d+$/.test(value)
    }

    return (
        <div className="blur">
            <div className="blur_block">
                <div onClick={() => { props.fync(false) }} className="blur_exit"> <img src={exit} alt="" /> </div>
                <div className="money_block">
                    <div className="money_title">–í—ã–≤–æ–¥ —Å—Ä–µ–¥—Å—Ç–≤</div>
                    <div className="money_text">–í–∞–º –¥–æ—Å—Ç—É–ø–Ω–æ: {money} —Ä—É–±–ª–µ–π</div>
                    <div className="auth_form_inputs">
                        <input
                            onChange={(e) => {
                                if (isNumeric(e.target.value) || e.target.value === '') {
                                    setCard(e.target.value)
                                }
                            }}
                            value={card}
                            placeholder="–í–≤–µ–¥–∏—Ç–µ –Ω–æ–º–µ—Ä –∫–∞—Ä—Ç—ã"
                            type="text" />
                        <input
                            value={getMoney}
                            onChange={(e) => {
                                if (parseInt(e.target.value) > money || !isNumeric(e.target.value)) {
                                    setGetMoney('')
                                }
                                else {
                                    setGetMoney(e.target.value)
                                }
                            }}
                            placeholder="–í–≤–µ–¥–∏—Ç–µ —Å—É–º–º—É –≤—ã–≤–æ–¥–∞"
                            type="text" />
                    </div>
                    <div onClick={() => { setGetMoney(`${money}`) }} className="all_in">–£–∫–∞–∑–∞—Ç—å –≤—Å—é —Å—É–º–º—É</div>
                    <div className="block_button_blue">–í—ã–≤–µ—Å—Ç–∏</div>
                </div>
            </div>
        </div>
    )
}

export default BlurMoney