import { IUser, UserAction, UserActionTypes } from "../../models/user";


const initialState: IUser = {
    id: 0,
    name: '',
    email: '',
    phone: '',
    money: 0,
    education: '',
    experience: 0,
    reputation: 0,
    rol: ''
}



export const RUser = (
    state = initialState,
    action: UserAction): IUser => {
    switch (action.type) {
        case UserActionTypes.AcceptUser:
            return {
                ...state, ...action.payload
            }
        default:
            return state
    }
}